import { motion } from "framer-motion"
import styled from "styled-components"

export const Wrapper = styled(motion.div)`
    background-color: ${props => props.theme.background.inverted};
    border-right: var(--line-width) solid ${props => props.theme.line.inverted};
    display: flex;
        flex-direction: column;
        justify-content: space-around;
    padding: var(--su);
    padding-left: calc(var(--su) + var(--nav-width));
    position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    z-index: 3;

    @media (min-width: 800px) {
        right: calc(50% - (var(--nav-width) / 2));
    }

`

export const Card = styled(motion.div)``

export const Date = styled.h6`
    color: ${props => props.theme.text.invertedMuted};
    display: none;
    margin-bottom: 16px;

    @media (min-width: 800px) {
        display: block;
    }

`

export const Title = styled.h3`
    margin-bottom: 12px;
    line-height: var(--line-height-tight);
`

export const Tagline = styled.h5`
    color: ${props => props.theme.text.invertedMuted};
    font-weight: 400;
    line-height: var(--line-height-paragraph);
`

export const CloseButton = styled.button`
    position: absolute;
        top: var(--su);
        right: var(--su);

    @media (min-width: 800px) {
        display: none;
    }

    svg {
        height: 16px;
        width: 16px;

        path {
            fill: ${props => props.theme.link.text.inverted};
            transition: fill 0.2s linear;
        }

    }

    &:hover svg path {
        transition: none;
        fill: ${props => props.theme.link.text.invertedHover};
    }

`