import React, { useRef, useState } from "react"
import * as Styled from "./styled"
import { Link } from "gatsby"
import { AnimatePresence } from "framer-motion"
import { Fade } from "../Animations"
import Logo from "../Logo"
import NavMenuVertical from "../NavMenuVertical"
import ThemeSwitcher from "../ThemeSwitcher"

export default () => {

    const wrapperVariant = {
        hidden: {
            y: "100%"
        },
        visible: {
            y: 0,
            transition: {
                duration: 2,
                ease: "anticipate"
            }
        }
    }    

    // Menu state

    const [menuOpen, setMenuOpen] = useState(false)

    // Toggle menu visibility

    const hideMenu = () => {
        setMenuOpen(false)
    }

    const showMenu = () => {
        setMenuOpen(true)
    }

    const toggleMenu = () => {
        menuOpen ? hideMenu() : showMenu()
    }

    // Ref passed to NavMenu component used to listen for clicks outside of the Nav component

    const navRef = useRef()

    return (
        <>
            <Styled.Wrapper
                ref={navRef}
                initial="hidden"
                animate="visible"
                variants={wrapperVariant}
            >
                <Styled.LinkGroup>
                    <Fade delay={0.5}>
                        <Link className="inverted hideUnderline" onClick={() => hideMenu()} to="/">
                            <Logo />
                        </Link>
                    </Fade>
                </Styled.LinkGroup>
                <Styled.LinkGroup>
                    <h6>
                        <Fade delay={0.5}>
                            <button className="inverted" onClick={() => toggleMenu()}>
                                <Styled.Text>Work</Styled.Text>
                            </button>
                        </Fade>
                    </h6>
                    <h6>
                        <Fade delay={0.5}>
                            <Link className="inverted" onClick={() => hideMenu()} to="/info">
                                <Styled.Text>Info</Styled.Text>
                            </Link>
                        </Fade>
                    </h6>
                    <h6>
                        <Fade delay={0.5}>
                            <a className="inverted" href="https://twitter.com/aaronshapiro">
                                <Styled.Text>Twitter</Styled.Text>
                            </a>
                        </Fade>
                    </h6>
                    <h6>
                        <Fade delay={0.5}>
                            <a className="inverted" href="mailto:aaron@aaron.mn">
                                <Styled.Text>Email</Styled.Text>
                            </a>
                        </Fade>
                    </h6>
                </Styled.LinkGroup>
                <Styled.LinkGroup>
                    <Fade delay={0.5}>
                        <ThemeSwitcher inverted="true" />
                    </Fade>
                </Styled.LinkGroup>

            </Styled.Wrapper>
            <AnimatePresence>
                { menuOpen && <NavMenuVertical menuOpen={menuOpen} hideMenu={hideMenu} navRef={navRef}/> }
            </AnimatePresence>
        </>
    
    )
}